/* eslint-disable react-hooks/exhaustive-deps */
import { useCallback, useState, useEffect, useRef } from "react";
import {
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Skeleton,
} from "@mui/material";
import moment from "moment";

import inactiveWA from "../../../../../assets/inactive-wa.svg";
import downloadIcon from "../../../../../assets/BusinessSettings/downloadIcon.svg";
import {
  getUtilization,
  getMonthlySessionTempConsumed,
  downloadMonthDateWiseReport,
  download_file,
  delete_file,
  get_health_report,
} from "../../../../../api/WhatsApp";
import { useLoader } from "../../../../../contexts/LoaderContext";
import { useWaitingLoader } from "../../../../../contexts/waitingLoaderContext";
import { useToastMessage } from "../../../../../contexts/ToastMessageContext";
import { useDailog } from "../../../../../contexts/DailogProvider";
import SelectMonth from "../../../../../Components/BusinessSettings/Whatsapp/SelectMonth";
import RangePopUp from "../RangePopUp";

// style imports
import useStyles from "../styles/muiStyles";

const DesktopWhatsStats = ({ waStatus }) => {
  // const sessionAverage = 31;
  const classes = useStyles();
  const [loadingUsage, setLoading] = useState(true);
  const [loadingReport, setLoadingReport] = useState(true);
  const [period, setPeriod] = useState("TODAY");
  const [
    tier,
    // setTier
  ] = useState(1);
  const [connectionStatus, setConnectionStatus] = useState();
  const [wAHealth, setWAHealth] = useState();
  // const [provider, setProvider] = useState();
  const [cis, setCIS] = useState(0);
  const [bis, setBIS] = useState(0);
  const [average, setAverage] = useState();
  const { handleClickOpen } = useDailog();
  const { openLoader, closeLoader } = useWaitingLoader();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const { cancelLoader } = useLoader();
  const [monthlyReport, setMonthlyReport] = useState([]);
  const businessId = localStorage.getItem("businessId");
  const [range, setRange] = useState([null, null]);
  const [startDate, endDate] = range;
  const [rangePicker, toggleRangePicker] = useState(false);

  const openRangePicker = () => {
    setRange([null, null]);
    toggleRangePicker(true);
  };

  const closeRangePicker = (event, reason) => {
    if (reason === "escapeKeyDown" || reason === "backdropClick") {
      setPeriod("TODAY");
    } else {
      setPeriod("CUSTOM");
    }
    toggleRangePicker(false);
  };

  const TierLabel = ({ index }) => (
    <div className="tier">
      <svg
        width="11"
        height="11"
        viewBox="0 0 11 11"
        fill="none"
        xmlns="//www.w3.org/2000/svg"
      >
        <circle
          cx="5.5"
          cy="5.5"
          r={0.5 + index}
          fill={index === 1 ? "#FF8719" : index === 2 ? "#32C5FF" : "#00CD39"}
        />
        <circle cx="5.5" cy="5.5" r="5" stroke="#B9B9B9" />
      </svg>
      <span className="label">{`TIER ${index}`}</span>
    </div>
  );

  const StatusLabel = ({ status }) =>
    status ? (
      <div className="status">
        {status.toLowerCase() === "good" && (
          <svg
            width="14"
            height="12"
            viewBox="0 0 14 12"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M13.0134 3.10551L4.11073 11.2663L0.767578 6.44966L3.2321 4.73908L4.61622 6.73324L10.9863 0.894043L13.0134 3.10551Z"
              fill="#5ECF70"
            />
          </svg>
        )}
        <span className="label">{status}</span>
      </div>
    ) : (
      <div className="status">
        <Skeleton width={55} />
      </div>
    );

  const ConnectionLabel = ({ status }) =>
    status ? (
      <div className="connection">
        {status ? (
          <svg
            width="14"
            height="12"
            viewBox="0 0 14 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M13.0134 3.10551L4.11073 11.2663L0.767578 6.44966L3.2321 4.73908L4.61622 6.73324L10.9863 0.894043L13.0134 3.10551Z"
              fill="#5ECF70"
            />
          </svg>
        ) : (
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M9.62882 1.98772L2.62882 9.98772L0.371094 8.01221L7.37109 0.012207L9.62882 1.98772Z"
              fill="#E02020"
            />
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.371176 1.98772L7.37118 9.98772L9.62891 8.01221L2.62891 0.012207L0.371176 1.98772Z"
              fill="#E02020"
            />
          </svg>
        )}
        <span className="label">
          {status === "connected" ? "Connected" : "Not Connected"}
        </span>
      </div>
    ) : (
      <div className="status">
        <Skeleton width={55} />
      </div>
    );

  const getMonthlyReportRef = useRef(async () => {
    const res = await getMonthlySessionTempConsumed(businessId);
    if (!res.success) {
      return setErrorMessage(res.error);
    }
    setMonthlyReport(res.payload);
    setLoadingReport(false);
  });

  const getRangeUtilization = async () => {
    false && console.log("period", period, startDate, endDate);
    let from =
      period === "CUSTOM" && startDate
        ? startDate
        : period === "TODAY"
        ? new Date()
        : null;
    let to =
      period === "CUSTOM" && endDate
        ? endDate
        : period === "TODAY"
        ? new Date()
        : null;
    from && from.setHours(0, 0, 0, 0);
    to && to.setHours(23, 59, 59, 999);
    if (from && to) {
      setLoading(true);
      const res = await getUtilization(from, to, businessId);
      console.log("res", res);
      if (!res.success) {
        cancelLoader();
        setLoading(false);
        return setErrorMessage(res.error);
      }
      cancelLoader();
      setCIS(res.payload.cis);
      setBIS(res.payload.bis);
      setAverage(res.payload.replyRate);
      setLoading(false);
    }
  };

  const monthFormat = (month) => moment(month).format("MMM YYYY");

  const downloadReport = async (month) => {
    openLoader();

    await downloadMonthDateWiseReport(businessId, { month })
      .then(async (response) => {
        try {
         const blob = await response.blob();
          if (blob.type.length <= 1) {
            // Create blob link to download
            const url = window.URL.createObjectURL(new Blob([blob]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute(
              "download",
              `SessionReport_${month}.pdf`
            );
            // Append to html link element page
            document.body.appendChild(link);
            // Start download
            link.click();
            // Clean up and remove the link
            link.parentNode.removeChild(link);
            setSuccessMessage("Report Downloaded");
          } else {
            throw new Error("No data found for selected month.");
          }
        } catch (err) {
          setErrorMessage(err.message);
        }
      })
      .catch((err) => {
        setErrorMessage(err.message);
      })
      .finally(() => {
        closeLoader();
      });
  };

  const getHealthReport = useCallback(async () => {
    try {
      const result = await get_health_report();
      const { account_status, health } = result.payload;
      let healthVal = "";
      if (health === "GREEN") {
        healthVal = "Good";
      } else if (health === "ORANGE") {
        healthVal = "Moderate";
      } else if (health === "RED") {
        healthVal = "Poor";
      } else if (health === "GREY") {
        healthVal = "Unable to Connect";
      } else {
        healthVal = health;
      }
      setWAHealth(healthVal);
      setConnectionStatus(account_status);
    } catch (error) {
      setErrorMessage(error.message);
    }
  }, []);

  useEffect(() => {
    waStatus && getRangeUtilization();
  }, [startDate, endDate, period, waStatus]);

  useEffect(() => {
    waStatus && getMonthlyReportRef.current();
  }, [waStatus]);

  useEffect(() => {
    waStatus && getHealthReport();
  }, [getHealthReport, waStatus]);

  return (
    <div className={classes["stacked-cards"]}>
      {!waStatus && (
        <div className="card-group inactive">
          <img src={inactiveWA} alt="inactive-wa" />
          <div className="info">
            <p className="desc">
              {
                "Your WhatsApp Business API account is not active yet. It usually takes upto 72 business hours for activation."
              }
            </p>
            <p className="desc">
              {"Please contact us on "}
              <Link href="mailto:support@easysocial.io" underline="hover">
                {"support@easysocial.io"}
              </Link>
              {" in case it is taking longer than expected."}
            </p>
          </div>
        </div>
      )}
      {waStatus && (
        <div className="card-group status">
          <h2 className="card-title">{"Live Status"}</h2>
          <div className="status-blocks">
            <div className={`block tier-${tier}`}>
              <h3 className="block-title">{"Messaging Tier"}</h3>
              <TierLabel index={tier} />
            </div>
            <div
              className={`block${
                wAHealth && wAHealth === "Good" ? " success" : ""
              }`}
            >
              <h3 className="block-title">{"WhatsApp Health"}</h3>
              <StatusLabel status={wAHealth} />
            </div>
            <div
              className={`block connection-${
                !connectionStatus ? "" : connectionStatus ? "success" : "failed"
              }`}
            >
              <h3 className="block-title">{"Account Status"}</h3>
              <ConnectionLabel status={connectionStatus} />
            </div>
          </div>
          <div className="footer-note">
            <p>
              {
                "TIER 1 allows 1000 outgoing messages to be sent in 24hrs. To reach next tier send 1000 messages for consecutive 3 days."
              }
            </p>
          </div>
        </div>
      )}
      {waStatus && (
        <div className="card-group">
          <RangePopUp
            open={rangePicker}
            closeDialog={closeRangePicker}
            range={range}
            setRange={setRange}
            wrapper={"desktop"}
          />
          <div className="card-header">
            <div className="left">
              <h3 className="card-title">{"Utilization"}</h3>
              {period === "TODAY" && (
                <h5 className="period">{moment().format("Do MMM YYYY")}</h5>
              )}
              {period === "CUSTOM" && startDate && endDate && (
                <h5 className="period">
                  {moment(startDate).format("Do MMM YYYY")}
                  {` to `}
                  {moment(endDate).format("Do MMM YYYY")}
                </h5>
              )}
            </div>
            <div className="right">
              <Link
                className={`change-period ${
                  period === "TODAY" ? "active" : ""
                }`}
                component="button"
                underline="none"
                onClick={() => {
                  setPeriod("TODAY");
                  setRange([null, null]);
                }}
              >
                {"TODAY"}
              </Link>
              <Link
                className={`change-period ${
                  period === "CUSTOM" ? "active" : ""
                }`}
                component="button"
                underline="none"
                onClick={() => openRangePicker()}
              >
                {"CUSTOM"}
              </Link>
            </div>
          </div>
          <div className="usage-blocks">
            <div className="block">
              <div className="block-header">
                <h3 className="title">{"Customer Initiated Sessions"}</h3>
                <svg
                  className="icon"
                  width="23"
                  height="23"
                  viewBox="0 0 23 23"
                  fill="none"
                  xmlns="//www.w3.org/2000/svg"
                >
                  <circle cx="11.5" cy="11.5" r="11.5" fill="#B0E9FF" />
                  <path
                    d="M11.6525 11.5217C12.9011 11.5217 13.9133 10.5095 13.9133 9.26087C13.9133 8.01223 12.9011 7 11.6525 7C10.4038 7 9.3916 8.01223 9.3916 9.26087C9.3916 10.5095 10.4038 11.5217 11.6525 11.5217Z"
                    stroke="white"
                    strokeWidth="1.2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    d="M8 14.9997C8 13.2606 9.3913 11.5215 11.6522 11.5215C13.913 11.5215 15.3043 13.2606 15.3043 14.9997"
                    stroke="white"
                    strokeWidth="1.2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              </div>
              <div className="block-body">
                <h4 className="value">{loadingUsage ? <Skeleton /> : cis}</h4>
                <h4 className="unit">
                  {loadingUsage ? <Skeleton /> : "Sessions"}
                </h4>
              </div>
            </div>
            <div className="block">
              <div className="block-header">
                <h3 className="title">{"Business Initiated Sessions"}</h3>
                <svg
                  className="icon"
                  width="23"
                  height="23"
                  viewBox="0 0 23 23"
                  fill="none"
                  xmlns="//www.w3.org/2000/svg"
                >
                  <circle cx="11.5" cy="11.5" r="11.5" fill="#B0E9FF" />
                  <path
                    d="M10.3076 10.0218C10.5826 10.0218 10.8056 9.79887 10.8056 9.52382V8.89302C10.8056 8.61801 10.5826 8.39502 10.3076 8.39502C10.0325 8.39502 9.80957 8.61798 9.80957 8.89302V9.52382C9.80957 9.79887 10.0326 10.0218 10.3076 10.0218Z"
                    fill="white"
                  />
                  <path
                    d="M12.167 10.0218C12.442 10.0218 12.665 9.79887 12.665 9.52382V8.89302C12.665 8.61801 12.442 8.39502 12.167 8.39502C11.8919 8.39502 11.6689 8.61798 11.6689 8.89302V9.52382C11.6689 9.79887 11.8919 10.0218 12.167 10.0218Z"
                    fill="white"
                  />
                  <path
                    d="M10.3076 12.8768C10.5826 12.8768 10.8056 12.6538 10.8056 12.3788V11.748C10.8056 11.473 10.5826 11.25 10.3076 11.25C10.0325 11.25 9.80957 11.473 9.80957 11.748V12.3788C9.80957 12.6538 10.0326 12.8768 10.3076 12.8768Z"
                    fill="white"
                  />
                  <path
                    d="M12.167 12.8768C12.442 12.8768 12.665 12.6538 12.665 12.3788V11.748C12.665 11.473 12.442 11.25 12.167 11.25C11.8919 11.25 11.6689 11.473 11.6689 11.748V12.3788C11.6689 12.6538 11.8919 12.8768 12.167 12.8768Z"
                    fill="white"
                  />
                  <path
                    d="M15.7476 16.0855C13.6731 16.0855 17.2129 16.0855 14.3495 16.0855V6.49801C14.3495 6.22299 14.1265 6 13.8515 6H8.62277C8.34775 6 8.12476 6.22296 8.12476 6.49801V16.0855C4.71899 16.0855 9.89447 16.0855 6.49801 16.0855C6.22296 16.0855 6 16.3085 6 16.5835C6 16.8586 6.22296 17.0815 6.49801 17.0815H8.6228H13.8515H15.7476C16.0226 17.0815 16.2456 16.8586 16.2456 16.5835C16.2456 16.3085 16.0226 16.0855 15.7476 16.0855ZM11.7218 16.0855H10.8024V15.1661H11.7218V16.0855ZM13.3535 16.0855H12.7178V14.6681C12.7178 14.3931 12.4948 14.1701 12.2198 14.1701H10.3044C10.0294 14.1701 9.80638 14.3931 9.80638 14.6681V16.0855H9.1208V6.99601H13.3535L13.3535 16.0855Z"
                    fill="white"
                  />
                </svg>
              </div>
              <div className="block-body">
                <h4 className="value">{loadingUsage ? <Skeleton /> : bis}</h4>
                <h4 className="unit">
                  {loadingUsage ? <Skeleton /> : "Sessions"}
                </h4>
              </div>
            </div>
            <div className="block">
              <div className="block-header">
                <h3 className="title">
                  {"Business Initiated Sessions - Reply %"}
                </h3>
                <svg
                  className="icon"
                  width="23"
                  height="23"
                  viewBox="0 0 23 23"
                  fill="none"
                  xmlns="//www.w3.org/2000/svg"
                >
                  <circle cx="11.5" cy="11.5" r="11.5" fill="#B0E9FF" />
                  <path
                    d="M10.3111 10.3669L10.3117 8.48321C10.3117 8.28781 10.1941 8.11161 10.0136 8.03682C9.83313 7.96204 9.62527 8.00326 9.48703 8.1414L7.14159 10.4856C6.95289 10.6743 6.95279 10.9801 7.14139 11.1688L9.48562 13.5143C9.57802 13.6068 9.70166 13.656 9.8274 13.656C9.88962 13.656 9.95233 13.6439 10.012 13.6192C10.1926 13.5445 10.3103 13.3684 10.3104 13.173L10.3109 11.3382C12.4041 11.5783 14.0352 13.3606 14.0352 15.517C14.0352 15.7839 14.2515 16.0002 14.5183 16.0002C14.7851 16.0002 15.0014 15.7839 15.0014 15.517C15.0014 12.8275 12.938 10.6111 10.3111 10.3669Z"
                    fill="white"
                  />
                </svg>
              </div>
              <div className="block-body">
                <h4 className="value">
                  {loadingUsage ? <Skeleton /> : average}
                </h4>
                <h4 className="unit">
                  {/*                   {loadingUsage ? (
                    <Skeleton />
                  ) : parseFloat(average.slice(0, -1)) > 0 ? (
                    parseFloat(average.slice(0, -1)) < sessionAverage ? (
                      `Lower than average ${sessionAverage}%`
                    ) : (
                      `Higher than average ${sessionAverage}%`
                    )
                  ) : null} nagendra temp*/}
                </h4>
              </div>
            </div>
          </div>
        </div>
      )}
      {waStatus && (
        <div className="card-group">
          <h3 className="card-title">{"Monthly Utilization Report"}</h3>
          <SelectMonth />
          <TableContainer>
            <Table aria-label="Monthly Report" className="reports">
              <TableHead>
                <TableRow>
                  <TableCell align="center">{"Duration"}</TableCell>
                  <TableCell align="center">
                    {"Customer"}
                    <br />
                    {"Initiated Sessions"}
                  </TableCell>
                  <TableCell align="center">
                    {"Business"}
                    <br />
                    {"Initiated Sessions"}
                  </TableCell>
                  <TableCell align="center">{"Report"}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {loadingReport ? (
                  <TableRow
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                    hover
                  >
                    <TableCell align="center" className="loading">
                      <Skeleton width={60} />
                    </TableCell>
                    <TableCell align="center" className="loading">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center" className="loading">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center" className="loading">
                      <Skeleton width={74} />
                    </TableCell>
                  </TableRow>
                ) : monthlyReport && monthlyReport.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan="4" align="center">
                      {"No Reports Available"}
                    </TableCell>
                  </TableRow>
                ) : (
                  monthlyReport &&
                  monthlyReport.map((report, index) => (
                    <TableRow
                      key={index}
                      sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                      hover
                    >
                      <TableCell align="center">
                        {monthFormat(report.month)}
                      </TableCell>
                      <TableCell align="center">{report.cis}</TableCell>
                      <TableCell align="center">{report.bis}</TableCell>
                      <TableCell align="center">
                        <Link
                          component="button"
                          underline="none"
                          className="report-download"
                          onClick={() => downloadReport(report.month)}
                        >
                          <img src={downloadIcon} alt="download" />
                          <span className="ms-1">{"download"}</span>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </TableContainer>
          {!loadingReport && monthlyReport && monthlyReport.length > 0 && (
            <div className="report-downloads">
              <Link
                component="button"
                underline="none"
                onClick={handleClickOpen}
              >
                {"Download older reports"}
              </Link>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default DesktopWhatsStats;
