import React from "react";

import { Dialog, DialogContent, TextField, Box } from "@mui/material";
import { StaticDateRangePicker, LocalizationProvider } from "@mui/lab";
import AdapterDateFns from "@mui/lab/AdapterDateFns";

import useStyles from "../styles/muiStyles";

const RangePopUp = ({
  open,
  closeDialog,
  range,
  setRange,
  wrapper = "mobile",
}) => {
  const classes = useStyles();

  return (
    <Dialog
      open={open}
      onClose={closeDialog}
      maxWidth="md"
      className={classes["range-popup"]}
    >
      <DialogContent>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <StaticDateRangePicker
            displayStaticWrapperAs={wrapper}
            value={range}
            onChange={(newValue) => {
              setRange(newValue);
              const [selectedStart, selectedEnd] = newValue;
              selectedStart !== null && selectedEnd !== null && closeDialog();
            }}
            maxDate={new Date()}
            minDate={new Date("2021-01-01")}
            renderInput={(startProps, endProps) => (
              <React.Fragment>
                <TextField {...startProps} />
                <Box sx={{ mx: 2 }}> to </Box>
                <TextField {...endProps} />
              </React.Fragment>
            )}
          />
        </LocalizationProvider>
      </DialogContent>
    </Dialog>
  );
};

export default RangePopUp;
