/* eslint-disable react-hooks/exhaustive-deps */
import {
  Fragment,
  FunctionComponent,
  useCallback,
  useEffect,
  useState,
} from "react";
import { Hidden } from "@material-ui/core";

import { get_wa_Status } from "../../../api/WhatsApp";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import MobileWhatsStats from "../../../Container/BusinessSettings/Whatsapp/WhatsStats/Mobile";
import DesktopWhatsStats from "../../../Container/BusinessSettings/Whatsapp/WhatsStats/Desktop";

import Layout from "../Layout";

interface IWhatsAppProps {}

const WhatsApp: FunctionComponent<IWhatsAppProps> = (props) => {
  const [waStatus, setWaStatus] = useState();
  const { setErrorMessage } = useToastMessage();
  const getWaStatus = useCallback(async () => {
    try {
      const result = await get_wa_Status();
      setWaStatus(result.success);
    } catch (error) {
      setErrorMessage(error.message);
    }
  }, []);

  useEffect(() => {
    getWaStatus();
  }, [getWaStatus]);

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <MobileWhatsStats waStatus={waStatus} />
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopWhatsStats waStatus={waStatus} />
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default WhatsApp;
