import React, { useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  Stack,
} from "@mui/material";
import moment from "moment";

import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { DatePicker, LocalizationProvider } from "@mui/lab";

import { useWaitingLoader } from "../../../contexts/waitingLoaderContext";

import { useDailog } from "../../../contexts/DailogProvider";
import { useToastMessage } from "../../../contexts";
import {
  downloadMonthDateWiseReport,
  download_file,
  delete_file,
} from "../../../api/WhatsApp";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
function SelectMonth(props) {
  const { open, handleClose } = useDailog();
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  const { openLoader, closeLoader } = useWaitingLoader();

  const [monthPicker, setMonthPicker] = useState(null);

  const [load, setLoad] = useState(false);

  const fetchData = async (event) => {
    setLoad(true);
    const businessId = localStorage.getItem("businessId");
    event.preventDefault();
    if (!monthPicker) {
      setLoad(false);
      handleClose();
      setErrorMessage("Select Month");
    } else {
      await downloadMonthDateWiseReport(businessId, {
        month: moment(monthPicker).format("YYYY-MM"),
      })
        .then(async (response) => {


          try {
            const blob = await response.blob();
            if (blob.type.length <= 1) {
              // Create blob link to download
              const url = window.URL.createObjectURL(new Blob([blob]));
              const link = document.createElement("a");
              link.href = url;
              link.setAttribute(
                "download",
                `SessionReport_${moment(monthPicker).format("YYYY-MM")}.pdf`
              );
              // Append to html link element page
              document.body.appendChild(link);
              // Start download
              link.click();
              // Clean up and remove the link
              link.parentNode.removeChild(link);
              setSuccessMessage("Report Downloaded");
            } else {
              throw new Error("No data found for selected month.");
            }
          } catch (err) {
            setLoad(false);
            setErrorMessage(err.message);
          }
        })
        .catch((err) => {
          setLoad(false);
        })
        .finally(() => {
          setLoad(false);
          closeLoader();
        });
    }
  };

  return (
    <div>
      <Dialog
        // fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
      >
        <DialogTitle>{load ? "" : "Select Month"}</DialogTitle>
        {!load && (
          <>
            <DialogContent style={{ paddingTop: "20px" }}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <Stack spacing={3}>
                  <DatePicker
                    views={["year", "month"]}
                    label="Year and Month"
                    minDate={new Date("2021-01-01")}
                    maxDate={new Date()}
                    value={monthPicker}
                    onChange={(newValue) => {
                      setMonthPicker(newValue);
                    }}
                    renderInput={(params) => (
                      <TextField {...params} helperText={null} />
                    )}
                  />
                </Stack>
              </LocalizationProvider>
            </DialogContent>
            <DialogActions>
              <Button onClick={fetchData}>Download</Button>
            </DialogActions>
          </>
        )}
        {load && (
          <DialogContent>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <CircularProgress />
              <span>Downloading</span>
            </Box>
          </DialogContent>
        )}
      </Dialog>
    </div>
  );
}

export default SelectMonth;
